@keyframes sway {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 00px 05px;
}

