.baby-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  clip-path: circle(22% at 50% 43%); 
}

.photo-frame-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  animation: sway 3s ease-in-out infinite;
  pointer-events: none; 
}

