/* src/pages/Gallery.css */

.gallery-container {
  padding: 40px 20px;
  text-align: center;
}

.gallery-title-container {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
}

.gallery-title-container h2 {
  position: relative;
  font-family: 'Gaegu', cursive;
  font-size: 2.5rem;
  color: #070707;
  margin: 0;
  z-index: 1;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

