.photo-grid {
  display: grid;
  grid-template-columns: 1fr; /* 1ì—´ë¡œ ë³€ê²½ */
  gap: 20px; /* ì„¸ë¡œ ê°„ê²©ì„ ì¡°ê¸ˆ ë” ì¤ë‹ˆë‹¤. */
  
  /* ì‚¬ì§„ì´ ë„ˆë¬´ ì»¤ì§€ì§€ ì•Šë„ë¡ ìµœëŒ€ ë„ˆë¹„ë¥¼ ì„¤ì •í•˜ê³  ì¤‘ì•™ ì •ë ¬í•©ë‹ˆë‹¤. */
  max-width: 500px;
  margin: 0 auto;
}

.photo-item {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  cursor: pointer;
  aspect-ratio: 1 / 1;
}

.photo-item:hover {
  transform: scale(1.05);
}

.photo-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
